@font-face {
    font-family: "File manager";
    src: url('../font/file_manager.ttf');
}

@diagramPercentBlock: @safePadding * 3;

.iconFileManager {
    font-family: "File manager" !important;
}

.favoritesStar {
    text-align: right !important;
 }

#pageMain {
}

.panelEx.right .item {
    color: #FEC500;
    padding-left: unit(@safePadding, px);
}

.panelEx.left .item:nth-child(n + 2) .component.layout div:last-child,
.panelEx.right .item div:first-letter,
.persistentMainEntries {
    text-transform: capitalize;
}

.authorizeBtn, .cancelBtn, .createBtn {
    text-transform: capitalize;
}

.placeholder {
    text-transform: capitalize;
}

.denseRightDisplay {
    .component.layout, .component.layout > div {
        display: block;
    }

    .item div {
        height: unit(@safePadding, px) !important;
        line-height: unit(@safePadding, px) !important;
    }

    .item div div:last-child {
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .item:first-child {
        // TODO: set height to 'name' field.
    }
}

.denseRightDisplay .item div:nth-child(even)  {
    color: #fff;
}

.panelSet .component.list.layoutList > .body > .item > .layout > .icon {
    min-width: unit(@panelTitleHeight, px);
}

.panelEx.main .item {
    overflow: hidden !important;
    width: unit(@panelMainWidth, px) !important;
    padding-right: unit(@safePadding, px) !important;
}

.listDirFileIcon {
}

.listDirNameField {
    text-overflow: ellipsis;
    white-space: nowrap;
    width: unit(@panelMainWidth - @safePadding * 7, px) !important;
    max-width: unit(@panelMainWidth - @safePadding * 7, px) !important;
    overflow: hidden;
}

.networkView .listDirNameField {
    width: unit(@panelMainWidth - @safePadding * 6, px) !important;
    max-width: unit(@panelMainWidth - @safePadding * 6, px) !important;
}

.panelEx.right .item {
    padding-top: unit(@safePadding / 2, px);
    overflow: hidden !important;
    width: unit(@panelWidth, px) !important;
    padding-right: unit(@safePadding, px) !important;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.panelEx.main .item {
    white-space: nowrap;
}

.panelEx.right .component.list.layoutList {
    position: static;
}

.infoStorageDiagram {
    border: unit(@safePadding / 2.5, px) solid #303030;
    margin: 0 auto;
    position: relative;
    top: unit(-@panelWidth / 2.5, px);
    width: unit(@panelHeight / 2.22, px) !important;
    height: unit(@panelHeight / 2.22, px) !important;
    text-align: center;
    -webkit-border-radius: unit(@panelHeight, px);
}

.infoStorageDiagram div {
    background: #303030;
    color: #FEC500;
    height: unit(@diagramPercentBlock, px);
    left: unit(@panelHeight / 2.22 / 2 - @diagramPercentBlock / 2 - @safePadding / 2.5, px);
    line-height: unit(@diagramPercentBlock, px);
    position: absolute;
    text-align: center;
    top: unit(@panelHeight / 2.22 / 2 - @diagramPercentBlock / 2 - @safePadding / 2.5, px);
    -webkit-border-radius: unit(@diagramPercentBlock / 2, px);
    width: unit(@diagramPercentBlock, px);
}

.hidden {
    display: none;
}

.scrollBar {
    height: unit(@panelMainHeight - @panelMainTitleHeight, px) !important;
    position: absolute;
    right: unit(@safePadding / 4, px);
    top: unit(@panelMainTitleHeight, px);
}

.cntIndicators {
    height: unit(@safePadding, px);
    line-height: unit(@safePadding, px) !important;
    padding-left: unit(@safePadding, px);
    position: relative;
    top: unit(-@panelWidth / 2.28, px);
    width: 100%;
    text-transform: capitalize;
}

.allocSpaceElem, .freeSpaceElem {
    display: inline-block;
    height: unit(@safePadding, px);
    margin-right: unit(@safePadding / 2, px);
    width: unit(@safePadding, px);
}

.allocSpaceElem {
    border: 1px solid #303030;
}

.freeSpaceElem {
    margin-left: unit(@safePadding * 7, px);
    border: 1px solid #303030;
}

.denseRightDisplay .body > .layoutList {
    height: unit(@panelHeight - @panelTitleHeight, px);
}

.cancelBtn, .createBtn {
    width: 50%;
}

.authorizeBtn {
    width: 100%;
}

.inactiveBtn {
    background: initial !important;
}

.input {
    color: inherit !important;
}
